.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  margin-top: 2px;
  margin-left: 5px;
  margin-right: 5px;
}
.Loading {
  text-align: center;
  margin: 20% 0;
}

@media (max-width: 580px) {
  .Loading {
    margin: 40% 0;
  }

  .showdata {
    display: flex;
    flex-direction: column;
    padding: 5%;

    text-align: center;
  }

  img {
    margin: auto 15%;
  }
}

.nav {
  padding-bottom: 10px;
  background-color: blue;
}
